html, body {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 200;
}

nav {
  background: -webkit-linear-gradient(45deg,#1a1e5a 0,#122d98 100%);
  background: -o-linear-gradient(45deg,#1a1e5a 0,#122d98 100%);
  background: linear-gradient(45deg,#1a1e5a 0,#122d98 100%);
}

.navbar {
  min-height: 50px;
}

.nav-logo {
  height: 25px;
}

.navbar-expand-md .navbar-nav .nav-link {
  color: #fafafa;
  font-size: 1.2rem;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 1.35rem;
  padding-right: 1.35rem;
}

.navbar-expand-md .navbar-nav .active-link {
  color: #f7b600;
}

.navbar-expand-md .navbar-nav .nav-link:hover {
  color: #f7b600;
}

.nav-search {
  color: #f7b600;
}

.nav-ribbon {
  background: -webkit-gradient(linear,left top, right top,color-stop(0, #f7b600),to(#f7de00));
  background: -webkit-linear-gradient(left,#f7b600 0,#f7de00 100%);
  background: -o-linear-gradient(left,#f7b600 0,#f7de00 100%);
  background: linear-gradient(to right,#f7b600 0,#f7de00 100%);
}

.navbar-nav {
  font-size: 2rem;
  padding-top: 1.8rem;
  padding-bottom: 1.8rem;
}

.nav-divider {
  color: #9b9b9b;
  margin-left: 1.5rem;
  margin-right: 3rem;
  height: 4.5rem;
}

.nav-indicator {
  position: absolute;
  -webkit-transition: -webkit-transform 0.35s ease-in-out;
  transition: -webkit-transform 0.35s ease-in-out;
  -o-transition: transform 0.35s ease-in-out;
  transition: transform 0.35s ease-in-out;
  transition: transform 0.35s ease-in-out, -webkit-transform 0.35s ease-in-out;
}

@media(min-width: 768px) {
  .nav-logo {
    height: 1.6rem;
  }
}

@media(min-width: 992px) {
  .nav-logo {
    height: 2rem;
  }
}

@media(min-width: 1200px) {
  .nav-logo { 
    height: 3rem;
  }
}

.pad-0 {
  padding: 0;
}


